<script>
import Login from '../components/version_1/Login.vue'
import Register from '../components/version_1/Register.vue'
import Paypal from '../components/version_1/Paypal.vue'
import Stripe from '../components/version_1/Stripe.vue'
import Detailspay from '../components/version_1/Detailspay.vue'
export default {
  components:{
   Register,
    Login,
   Paypal,
   Stripe,
   Detailspay
},
data(){
  return{
    val : true
  }
},
methods : {
  getval(){
    this.val = !this.val
  }
},
computed: {
authentication: function(){
  if (sessionStorage.getItem('token') == null){
    return false
}else{
    return true
}
}
}
}
</script>
